@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');



.button-area{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;

}

.link{
    color: #ffffff;
    font-size: 1.4em;
    font-weight: 500;
    left: 0;
    right: 0;
}
a:hover{
    color: #fff;
    text-decoration: none;
}
@media(max-width:576px){
    .link{
        font-size: 1.2em;
    }
}