
@font-face{
    font-family: gravity-falls;
    src: url('../../fonts/gravitation_falls.ttf');
}
header.header{
    padding: 20px 0;
    margin-bottom: 20px 0;
    border-bottom: 5px solid #1f093a;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    background-image:url("../../images/background/backgroundGif.gif");
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
    height:300px;
    margin-bottom: 5%;
}
div.line{
    color: #fff;
    height: 2px;
    width: 20%;
}
h1.page-name{
    text-align: center;
    margin: 0;
    text-align: center;
    font-family:gravity-falls;
    font-size: 3.0em;
    letter-spacing: 3px;
    color:#fff;
}
i.fa-spider,.fa-cat{
    color: #273036;
    font-size: 2.0em;
    color: #fff; 
    margin: 0px 5px;
}
img.wave{
    width: 100%;
    height:20%;
}

@media(max-width:576px){
    header.header{
        height:200px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position:center;
        padding-top: 20px 0;
        margin-bottom: 10%;
        border-bottom:1px solid rgba(209, 206, 206, 0.794);
    }
    h1.page-name{
        font-size:1.8em;
    }
}
