*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
}
div#div{
    padding: 10% 0;
}
body{
    background-color:#fff;
}
h1{
    font-size: 3.1em;
    color: #000;
    padding: 20px 5px;
}

#div p{
    font-size: 2.0em;
    color: #000; 
    padding: 10px 5px;
}
.form-control{
    text-align: center;
    width: 50%;
    padding: 20px;
    border: 1px solid #0009;
}

div.button-area{
    width: auto;
    height: auto;
    padding: 20px 0;
}
button#button{
    text-transform: uppercase;
}

div.error-message-area{
    padding: 20px 0;
}
span.message{
    font-size: 1.6em;
    font-weight: 600;
    letter-spacing: 2px;
}
span.text-alert{
    color: rgb(255, 81, 0);

}
span.text{
    color: #fff;
    font-size: 1.05em;
    font-weight: 500;
    padding: 10px 0;
}

button.btn{
    width: 50%;
    margin: 4px 0;
}
button#btn-nl{
    width: 20%;
}


@media(max-width:576px){
    .form-control{
        text-align: center;
        position: relative;
        width: 100%;
        margin:5px;
    }
    span.message{
        font-size: 1.3em;
        padding: 0 5px;
    }
    button.btn{
        margin: 10px 0;
    }
    p{
        font-size: 1.4em;
        margin:10px 5px;
        text-align: center;
    }
    button#btn-nl{
        width: 50%;
    }
}

.container-fluid{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


div.navbar{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    background-color:rgb(0, 0, 0);
    padding: 0;
    padding: 5px;
}
.logo-mobile{
    display: none;
}
.blue{
    color: #0b23ff;
}
nav#navbar{
    background-color:#0000;
    width:auto;
}
div.dropdown-menu.show{
    background-color: black;
    text-align: center;
}
#navbarNavDropdown a{
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
}
a.nav-link{
    font-family: 'Roboto', sans-serif;
    font-size: 1.0em;
    text-transform: uppercase;
}
a.nav-link:hover{
    background-color: rgb(30, 30, 30,.9);
    border-radius: 5%;
    transition: .3s ease-in-out;
}
.fa-ghost.logo-desktop{
    color: #fff;
    font-size: 2.1em;
    padding: 0 10px;
    animation: walkX 5s infinite;
}
.fa-ghost{
    margin: 0px 2px;
}
.fa-bars,.fa-times{
    color: #fff;
}
.fa-home,.fa-question,.fa-info{
    color:#8147c8;
}
button.navbar-toggler{
    background-color: rgb(30, 30, 30,.9);
}
button.navbar-toggler:focus{
    outline:none;
}
/*keyframe*/
@keyframes walkX{
    0%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(50px);
    }
    100%{
        transform: translateX(0);
    }
}
@media(max-width:576px){
    div.navbar{
        display:block;
    }
    .logo-desktop{
        display: none;
    }
    .fa-ghost.logo-mobile{
        font-size: 1.8em;
        animation:walkX 5s infinite;
    }
}

@media(max-width:992px){
    div.navbar{
        display:block;
    }
    .logo-desktop{
        display: none;
    }
    .logo-mobile{
        display: block;
    }
    .fa-ghost.logo-mobile{
        font-size: 1.8em;
        animation:walkX 5s infinite;
    }
}

@font-face{
    font-family: gravity-falls;
    src: url(/the-riddles/static/media/gravitation_falls.4b766b08.ttf);
}
header.header{
    padding: 20px 0;
    margin-bottom: 20px 0;
    border-bottom: 5px solid #1f093a;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    background-image:url(/the-riddles/static/media/backgroundGif.ac36d8c7.gif);
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
    height:300px;
    margin-bottom: 5%;
}
div.line{
    color: #fff;
    height: 2px;
    width: 20%;
}
h1.page-name{
    text-align: center;
    margin: 0;
    text-align: center;
    font-family:gravity-falls;
    font-size: 3.0em;
    letter-spacing: 3px;
    color:#fff;
}
i.fa-spider,.fa-cat{
    color: #273036;
    font-size: 2.0em;
    color: #fff; 
    margin: 0px 5px;
}
img.wave{
    width: 100%;
    height:20%;
}

@media(max-width:576px){
    header.header{
        height:200px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position:center;
        padding-top: 20px 0;
        margin-bottom: 10%;
        border-bottom:1px solid rgba(209, 206, 206, 0.794);
    }
    h1.page-name{
        font-size:1.8em;
    }
}

/*font-family: 'Poppins', sans-serif;*/



section.content{
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    position: relative;
    margin: 20px 10px;
    padding: 0;
}
section.content h2{
    padding:10px 0;
    margin: 5px 5px;
    text-align: left;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}
section.content h3{
    padding:10px 0;
    margin: 10px 5px;
    text-align: left;
    font-family: 'Poppins', sans-serif;
}

section.content p{
    text-align: left;
    color: #212529;
    font-weight:500;
    font-size: 20px;
    margin: 5px 5px;
    padding:0;
}
img.image{
    display:block;
    width: 60%;
    margin: 20px 5px;
    height:auto;
}
figcaption{
    text-align: left;
    margin: 5px 5px;
    padding: 5px 0;
    font-weight: 600;
    font-size: 14px;
    background-color: #f4f4f4;
}
section.content p a{
    margin: 10px 0;
    font-size: 1.070em;
}
section.content a:hover{
    color:dodgerblue;
}
span.text-red{
    color: red;
    font-weight: bolder;
}
ol.ol{
    margin: 0 20px;
}
.ol li{
    font-size: 18px;
    margin: 5px 5px;
    text-align: left;
}

.fa-eye{
    animation: switchOpacity 3s infinite ease-in-out;
}
i#fa-cat{
    color: black;
    font-size: 24px;
    margin-bottom: 2px;
}


@keyframes switchOpacity{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
@media(max-width:576px){
    div.grid-div{
        display: block;
    }
    div#content,div.articles-link{
        grid-area: none;
    }
    div.articles-links{
        border:none;
        position: relative;
        margin: 30px 10px;
    }
    h2.title{
        font-size: 1.6em;
    }
    img.image{
        width: 100%;
        margin: 20px 0;
    }
    img.hqs{
        position: relative;
        display: block;
        width:80%;
        height: 20%;
    }
}


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    left: 0;
    right: 0;
}

footer.footer{
    position:relative;
    bottom: 0;
    width:100%;
    height:auto;
    padding: 10px 0;
    display: block;
    padding:0;
    margin-top: 2%;
}

   

div.flex-div{
    position: relative;
    background-color:rgb(24,24,24);
    display: flex;
    justify-content: baseline;
    align-items: baseline;
    padding: 10px;
}
.footer-item{
    width: 50%;
    display: block;
    margin: 0px 10px;
    padding:10px 10px;
}
.footer-item:hover{
    background-color:rgba(18,18,18,.90);
    transition: .3s ease-in-out;
} 

div.content-footer h4{
    color: #fff;
    text-align: left;
    text-transform: uppercase;
    margin:0;
    padding: 0px 5px;
    letter-spacing: 1px;
    font-family: 'Roboto', sans-serif;
    animation: switchShadow 3s infinite;
}

div.content-footer p{
    margin: 5px 0;
    text-align: left;
    color: #fff;
    font-size: 16px;
}
b{
    letter-spacing: 1px;
    font-weight: 600;
}

.fa-code,.fa-info,.fa-github{
    font-size: 20px;
    color: #fff;
}
.fa-github{
    margin-right: 5px;
}
a.link-footer{
    color: #fff;
    text-decoration: none;
    font-size: 16px;
}
.dev-text{
    position: relative;
    display: block;
    width: auto;
    background-color:rgb(16,16,16);
    text-align: center;
    padding: 10px 0;
}
span.texto{
    color: #fff;
    font-size: 1.05em;
    text-align: center;
}
.fa-heart{
    color: rgb(255, 0, 55);
}
@keyframes SwitchShadow{
    0%{
       text-shadow: none;
    }
    50%{
       text-shadow: 2px 2px 2px #522d80;
    }
    100%{
       text-shadow: none;
    }
}


@media(max-width:576px){
    footer.footer{
        position:relative;
        bottom: 0;
        width:100%;
        height:20%;
        padding: 10px 0;
        display: block;
        padding:0;
    }
    div.flex-div{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: auto;
    }
    div.footer-item{
        width:auto;
    }
    .line{
        width: 19%;
        animation: switchWidthMobile 3s infinite;
    }
}

@keyframes switchWidthMobile{
    0%{
        width: 19%;
    }
    50%{
        width: 0%;
    }
    100%{
        width: 19%;
    }
}


div.articles-links{
    grid-area: links;
    margin:6% 0;
    padding:0;
    background-image: url(/the-riddles/static/media/wave.80307cda.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

div.matters{
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    align-items: baseline;
}

i.fa-newspaper, .fa-quidditch{
    font-size: 1.8em;
    margin: 0 5px;
}

div.article-thumbnail-area{
    display: flex;
    justify-content:baseline;
    align-items:baseline;
    flex-wrap: wrap;
}
div.article-thumbnail{
    display:flex;
    flex-direction: column;
    width: 18em;
    height:100%;
    border: 1px solid #0004;
    border-radius:.25rem;
    margin:20px;
}
img.thumbnail-image{
    position: relative;
    width:286px;
    height:214px;
}
div.article-thumbnail-texts{
    position: relative;
    padding:20px;
    height:320px;
    width: 100%;
}
div.article-thumbnail-texts p{
    text-align:left;
    font-size: 18px;
}
div.article-thumbnail-texts h4{
    text-align: left;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}
div.view-button-area{
    height: 100px;
    padding: 20px 0;
    margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

a.view-button{
    background-color: black;
    color: #fff;
    padding:.375rem .75rem;
    border-radius: .25rem;
    margin: 10px 20px;
}
.fly{
    animation: fly 3s infinite;
}
@keyframes fly{
    0%{
        transform:translateY(0);
    }
    25%{
        transform:rotateY(90deg);
    }
    50%{
        transform: translateY(-20px) ;
    }
    100%{
        transform: translateY(0);
    }
}
 @media(max-width:576px){
    div.articles-links{
        margin:40px 0;
        background-image: none;
        padding:0;
    }
    div.article-thumbnail-area{
        display: flex;
        justify-content:center;
        align-items: baseline;
        flex-wrap: wrap;
    }
    div.article-thumbnail{
        background-image: url(/the-riddles/static/media/waveMobile.420757c3.svg);
        background-repeat: no-repeat;
        background-size: cover;
    }
    div.article-thumbnail-texts{
        height: auto;
        padding:20px;
    }
    div.view-button-area{
        padding:0;
        margin: 20px 0;
    }
 }
