@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

div.navbar{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    background-color:rgb(0, 0, 0);
    padding: 0;
    padding: 5px;
}
.logo-mobile{
    display: none;
}
.blue{
    color: #0b23ff;
}
nav#navbar{
    background-color:#0000;
    width:auto;
}
div.dropdown-menu.show{
    background-color: black;
    text-align: center;
}
#navbarNavDropdown a{
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
}
a.nav-link{
    font-family: 'Roboto', sans-serif;
    font-size: 1.0em;
    text-transform: uppercase;
}
a.nav-link:hover{
    background-color: rgb(30, 30, 30,.9);
    border-radius: 5%;
    transition: .3s ease-in-out;
}
.fa-ghost.logo-desktop{
    color: #fff;
    font-size: 2.1em;
    padding: 0 10px;
    animation: walkX 5s infinite;
}
.fa-ghost{
    margin: 0px 2px;
}
.fa-bars,.fa-times{
    color: #fff;
}
.fa-home,.fa-question,.fa-info{
    color:#8147c8;
}
button.navbar-toggler{
    background-color: rgb(30, 30, 30,.9);
}
button.navbar-toggler:focus{
    outline:none;
}
/*keyframe*/
@keyframes walkX{
    0%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(50px);
    }
    100%{
        transform: translateX(0);
    }
}
@media(max-width:576px){
    div.navbar{
        display:block;
    }
    .logo-desktop{
        display: none;
    }
    .fa-ghost.logo-mobile{
        font-size: 1.8em;
        animation:walkX 5s infinite;
    }
}

@media(max-width:992px){
    div.navbar{
        display:block;
    }
    .logo-desktop{
        display: none;
    }
    .logo-mobile{
        display: block;
    }
    .fa-ghost.logo-mobile{
        font-size: 1.8em;
        animation:walkX 5s infinite;
    }
}