@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
}
div#div{
    padding: 10% 0;
}
body{
    background-color:#fff;
}
h1{
    font-size: 3.1em;
    color: #000;
    padding: 20px 5px;
}

#div p{
    font-size: 2.0em;
    color: #000; 
    padding: 10px 5px;
}
.form-control{
    text-align: center;
    width: 50%;
    padding: 20px;
    border: 1px solid #0009;
}

div.button-area{
    width: auto;
    height: auto;
    padding: 20px 0;
}
button#button{
    text-transform: uppercase;
}

div.error-message-area{
    padding: 20px 0;
}
span.message{
    font-size: 1.6em;
    font-weight: 600;
    letter-spacing: 2px;
}
span.text-alert{
    color: rgb(255, 81, 0);

}
span.text{
    color: #fff;
    font-size: 1.05em;
    font-weight: 500;
    padding: 10px 0;
}

button.btn{
    width: 50%;
    margin: 4px 0;
}
button#btn-nl{
    width: 20%;
}


@media(max-width:576px){
    .form-control{
        text-align: center;
        position: relative;
        width: 100%;
        margin:5px;
    }
    span.message{
        font-size: 1.3em;
        padding: 0 5px;
    }
    button.btn{
        margin: 10px 0;
    }
    p{
        font-size: 1.4em;
        margin:10px 5px;
        text-align: center;
    }
    button#btn-nl{
        width: 50%;
    }
}
