@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    left: 0;
    right: 0;
}

footer.footer{
    position:relative;
    bottom: 0;
    width:100%;
    height:auto;
    padding: 10px 0;
    display: block;
    padding:0;
    margin-top: 2%;
}

   

div.flex-div{
    position: relative;
    background-color:rgb(24,24,24);
    display: flex;
    justify-content: baseline;
    align-items: baseline;
    padding: 10px;
}
.footer-item{
    width: 50%;
    display: block;
    margin: 0px 10px;
    padding:10px 10px;
}
.footer-item:hover{
    background-color:rgba(18,18,18,.90);
    transition: .3s ease-in-out;
} 

div.content-footer h4{
    color: #fff;
    text-align: left;
    text-transform: uppercase;
    margin:0;
    padding: 0px 5px;
    letter-spacing: 1px;
    font-family: 'Roboto', sans-serif;
    animation: switchShadow 3s infinite;
}

div.content-footer p{
    margin: 5px 0;
    text-align: left;
    color: #fff;
    font-size: 16px;
}
b{
    letter-spacing: 1px;
    font-weight: 600;
}

.fa-code,.fa-info,.fa-github{
    font-size: 20px;
    color: #fff;
}
.fa-github{
    margin-right: 5px;
}
a.link-footer{
    color: #fff;
    text-decoration: none;
    font-size: 16px;
}
.dev-text{
    position: relative;
    display: block;
    width: auto;
    background-color:rgb(16,16,16);
    text-align: center;
    padding: 10px 0;
}
span.texto{
    color: #fff;
    font-size: 1.05em;
    text-align: center;
}
.fa-heart{
    color: rgb(255, 0, 55);
}
@keyframes SwitchShadow{
    0%{
       text-shadow: none;
    }
    50%{
       text-shadow: 2px 2px 2px #522d80;
    }
    100%{
       text-shadow: none;
    }
}


@media(max-width:576px){
    footer.footer{
        position:relative;
        bottom: 0;
        width:100%;
        height:20%;
        padding: 10px 0;
        display: block;
        padding:0;
    }
    div.flex-div{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: auto;
    }
    div.footer-item{
        width:auto;
    }
    .line{
        width: 19%;
        animation: switchWidthMobile 3s infinite;
    }
}

@keyframes switchWidthMobile{
    0%{
        width: 19%;
    }
    50%{
        width: 0%;
    }
    100%{
        width: 19%;
    }
}

