div.articles-links{
    grid-area: links;
    margin:6% 0;
    padding:0;
    background-image: url("../../images/background/wave.svg");
    background-repeat: no-repeat;
    background-size: cover;
}

div.matters{
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    align-items: baseline;
}

i.fa-newspaper, .fa-quidditch{
    font-size: 1.8em;
    margin: 0 5px;
}

div.article-thumbnail-area{
    display: flex;
    justify-content:baseline;
    align-items:baseline;
    flex-wrap: wrap;
}
div.article-thumbnail{
    display:flex;
    flex-direction: column;
    width: 18em;
    height:100%;
    border: 1px solid #0004;
    border-radius:.25rem;
    margin:20px;
}
img.thumbnail-image{
    position: relative;
    width:286px;
    height:214px;
}
div.article-thumbnail-texts{
    position: relative;
    padding:20px;
    height:320px;
    width: 100%;
}
div.article-thumbnail-texts p{
    text-align:left;
    font-size: 18px;
}
div.article-thumbnail-texts h4{
    text-align: left;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}
div.view-button-area{
    height: 100px;
    padding: 20px 0;
    margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

a.view-button{
    background-color: black;
    color: #fff;
    padding:.375rem .75rem;
    border-radius: .25rem;
    margin: 10px 20px;
}
.fly{
    animation: fly 3s infinite;
}
@keyframes fly{
    0%{
        transform:translateY(0);
    }
    25%{
        transform:rotateY(90deg);
    }
    50%{
        transform: translateY(-20px) ;
    }
    100%{
        transform: translateY(0);
    }
}
 @media(max-width:576px){
    div.articles-links{
        margin:40px 0;
        background-image: none;
        padding:0;
    }
    div.article-thumbnail-area{
        display: flex;
        justify-content:center;
        align-items: baseline;
        flex-wrap: wrap;
    }
    div.article-thumbnail{
        background-image: url("../../images/background/waveMobile.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }
    div.article-thumbnail-texts{
        height: auto;
        padding:20px;
    }
    div.view-button-area{
        padding:0;
        margin: 20px 0;
    }
 }