@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
/*font-family: 'Poppins', sans-serif;*/



section.content{
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    position: relative;
    margin: 20px 10px;
    padding: 0;
}
section.content h2{
    padding:10px 0;
    margin: 5px 5px;
    text-align: left;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}
section.content h3{
    padding:10px 0;
    margin: 10px 5px;
    text-align: left;
    font-family: 'Poppins', sans-serif;
}

section.content p{
    text-align: left;
    color: #212529;
    font-weight:500;
    font-size: 20px;
    margin: 5px 5px;
    padding:0;
}
img.image{
    display:block;
    width: 60%;
    margin: 20px 5px;
    height:auto;
}
figcaption{
    text-align: left;
    margin: 5px 5px;
    padding: 5px 0;
    font-weight: 600;
    font-size: 14px;
    background-color: #f4f4f4;
}
section.content p a{
    margin: 10px 0;
    font-size: 1.070em;
}
section.content a:hover{
    color:dodgerblue;
}
span.text-red{
    color: red;
    font-weight: bolder;
}
ol.ol{
    margin: 0 20px;
}
.ol li{
    font-size: 18px;
    margin: 5px 5px;
    text-align: left;
}

.fa-eye{
    animation: switchOpacity 3s infinite ease-in-out;
}
i#fa-cat{
    color: black;
    font-size: 24px;
    margin-bottom: 2px;
}


@keyframes switchOpacity{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
@media(max-width:576px){
    div.grid-div{
        display: block;
    }
    div#content,div.articles-link{
        grid-area: none;
    }
    div.articles-links{
        border:none;
        position: relative;
        margin: 30px 10px;
    }
    h2.title{
        font-size: 1.6em;
    }
    img.image{
        width: 100%;
        margin: 20px 0;
    }
    img.hqs{
        position: relative;
        display: block;
        width:80%;
        height: 20%;
    }
}

